/* src/App.css */

.App {
    text-align: center;
    font-family: Arial, sans-serif;
}

.gallery {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 20px;
}

.gallery-item {
    text-decoration: none;
    color: black;
    transition: transform 0.2s;
}

    .gallery-item:hover {
        transform: scale(1.05);
    }

.item {
    border: 1px solid #ccc;
    border-radius: 8px;
    padding: 20px;
    width: 150px;
    background: #f9f9f9;
}

    .item h2 {
        margin-bottom: 5px;
        font-size: 1.2em;
    }

    .item h3 {
        margin-bottom: 10px;
        font-size: 1em;
        color: #555;
    }

    .item p {
        margin: 0;
    }
